import { createRestaurantItemTemplate } from '../../templates/template';

class FavoriteRestaurantSearchView {
  getTemplate() {
    return `
        <div class="content">
          <input id="query" type="text">
          <h2 class="content__heading">Your Liked Restaurants</h2>      
              <div id="restaurants" class="restaurants">
          </div>
        </div>
        `;
  }

  runWhenUserIsSearching(callback) {
    document.getElementById('query').addEventListener('change', (event) => {
      callback(event.target.value);
    });
  }

  showRestaurants(restaurants) {
    this.showFavoriteRestaurants(restaurants);
  }

  showFavoriteRestaurants(restaurants = []) {
    let html;
    if (restaurants && restaurants.length > 0) {
      html = restaurants.reduce((carry, restaurant) => carry.concat(createRestaurantItemTemplate(restaurant)), '');
    } else {
      html = this._getEmptyRestaurantsTemplate();
    }

    document.getElementById('restaurants').innerHTML = html;

    document.getElementById('restaurants').dispatchEvent(new Event('restaurants:updated'));
  }

  _getEmptyRestaurantsTemplate() {
    return '<div class="resto-item__not__found resto__not__found">Tidak ada restoran untuk ditampilkan</div>';
  }
}

export default FavoriteRestaurantSearchView;
